import React from 'react'
// import {useStyles} from '../useStyles'
import {Grid,Button,makeStyles} from '@material-ui/core'
import { cyan, teal } from '@material-ui/core/colors'
import {categoryList as list} from '../data/lists'

const useStyles = makeStyles(theme =>({
    catBtn:{
        margin:'7px',
        color: theme.palette.getContrastText(teal[200]),
        // backgroundColor: cyan[500],
        '&:hover': {
        backgroundColor: teal[700],
        color: theme.palette.getContrastText(teal[600]),
        }
    },
    CountNews:{
        display:'flex',
        justifyContent:'space-between',
        color:'#564c4c'
    },
}))
export const CategoryList = ({selected}) => {
    const classes = useStyles()
    return (
        <Grid item xs={12} style={{ marginBottom:'12px'}}>
            {Object.values(list).map((itm,ind)=>
                <Button variant={selected === itm ? 'contained' : 'outlined'} color="primary" key={ind} className={classes.catBtn} style={{ margin:'7px'}}>
                    {itm}
                </Button>
            )}
            
            <CountNews />
        </Grid>
    )
}


const CountNews = () => {
    const classes = useStyles()
    return(
        <Grid className={classes.CountNews}>
            <p>Found 3456 Results</p>
            <p>1 of 231 pages</p>
        </Grid>
    )
}

